//    (  )
export const getProperties = async (
  pageSize: number = 1000, //    
  lastDoc?: any // QueryDocumentSnapshot<DocumentData>
): Promise<{ properties: Property[]; lastDoc: any | null }> => {
  try {
    console.log('===     ===');
    console.log('Firebase  ID:', db.app.options.projectId);
    console.log(' :', PROPERTIES_COLLECTION);
    
    // Firebase    ()
    try {
      const { getCurrentUser } = await import('./authService');
      const currentUser = getCurrentUser();
      console.log('  :', currentUser?.uid || '');
    } catch (authError) {
      console.log('    ():', authError);
    }
    
    //     (limit )
    const querySnapshot = await getDocs(collection(db, PROPERTIES_COLLECTION));
    
    console.log('=== Firestore   ===');
    console.log('  :', querySnapshot.docs.length);
    console.log(' ?', querySnapshot.empty);
    console.log('  :', querySnapshot.docs.map((doc, index) => ({
      index,
      id: doc.id,
      exists: doc.exists(),
      data: doc.data()
    })));
    
    const properties: Property[] = [];

    let index = 0;
    querySnapshot.forEach((doc: QueryDocumentSnapshot<DocumentData>) => {
      console.log(` ${index + 1}  :`, doc.id);
      const data = doc.data();
      console.log(` Firebase :`, data);
      const property = {
        id: doc.id,
        ...data,
        //   (Firebase    )
        price: data.price || data.rentPrice || 0,
        deposit: data.deposit || 0,
        createdAt: safeToDate(data.createdAt),
        updatedAt: safeToDate(data.updatedAt),
        isActive: data.isActive !== false // isActive  true  true 
      } as Property;
      
      console.log(`  ${index + 1}:`, {
        id: property.id,
        title: property.title,
        address: property.address,
        type: property.type,
        price: property.price,
        deposit: property.deposit,
        isActive: property.isActive
      });
      
      //    ( )
      properties.push(property);
      index++;
    });

    const lastVisible = querySnapshot.docs[querySnapshot.docs.length - 1] || null;

    console.log('===   ===');
    console.log('   :', properties.length, '');
    console.log('  :', properties.map(p => ({
      id: p.id,
      title: p.title,
      address: p.address,
      isActive: p.isActive
    })));
    console.log('===     ===');
    
    return { properties, lastDoc: lastVisible };
  } catch (error) {
    console.error('===     ===');
    console.error(':', error);
    console.error(' :', {
      code: (error as any)?.code,
      message: (error as any)?.message,
      stack: (error as any)?.stack
    });
    
    //    
    let errorMessage = '     .';
    if ((error as any)?.code === 'permission-denied') {
      errorMessage = '   . Firestore   .';
    } else if ((error as any)?.code === 'unauthenticated') {
      errorMessage = ' . .';
    } else if ((error as any)?.code === 'unavailable') {
      errorMessage = 'Firebase     .    .';
    }
    
    throw new Error(errorMessage);
  }
};

//   
export const getProperty = async (id: string): Promise<Property | null> => {
  try {
    const docRef = doc(db, PROPERTIES_COLLECTION, id);
    const docSnap = await getDoc(docRef);

    if (docSnap.exists()) {
      const data = docSnap.data();
      
      return {
        id: docSnap.id,
        ...data,
        createdAt: safeToDate(data.createdAt),
        updatedAt: safeToDate(data.updatedAt)
      } as Property;
    } else {
      return null;
    }
  } catch (error) {
    console.error('  :', error);
    throw new Error('    .');
  }
};

//  
export const updateProperty = async (id: string, updates: Partial<Property>): Promise<void> => {
  try {
    const docRef = doc(db, PROPERTIES_COLLECTION, id);
    await updateDoc(docRef, {
      ...updates,
      updatedAt: new Date()
    });
  } catch (error) {
    console.error('  :', error);
    throw new Error('    .');
  }
};

//   ( )
export const deleteProperty = async (id: string): Promise<void> => {
  try {
    const docRef = doc(db, PROPERTIES_COLLECTION, id);
    await updateDoc(docRef, {
      isActive: false,
      updatedAt: new Date()
    });
  } catch (error) {
    console.error('  :', error);
    throw new Error('    .');
  }
};

//  
export const searchProperties = async (
  searchTerm: string,
  propertyType?: string,
  transactionType?: string,
  maxPrice?: number
): Promise<Property[]> => {
  try {
    let q = query(
      collection(db, PROPERTIES_COLLECTION)
    );

    //   
    if (propertyType) {
      q = query(q, where('propertyType', '==', propertyType));
    }
    if (transactionType) {
      q = query(q, where('type', '==', transactionType));
    }
    if (maxPrice) {
      q = query(q, where('price', '<=', maxPrice));
    }

    const querySnapshot = await getDocs(q);
    const properties: Property[] = [];

    querySnapshot.forEach((doc) => {
      const data = doc.data();
      
      const property = {
        id: doc.id,
        ...data,
        createdAt: safeToDate(data.createdAt),
        updatedAt: safeToDate(data.updatedAt)
      } as Property;

      //   (, ,  )
      if (!searchTerm || 
          property.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
          property.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
          property.address.toLowerCase().includes(searchTerm.toLowerCase())) {
        properties.push(property);
      }
    });

    return properties;
  } catch (error) {
    console.error('  :', error);
    throw new Error('    .');
  }
};

