export const getPropertiesByLocation = async (
  centerLat: number,
  centerLng: number,
  radiusKm: number = 10
): Promise<Property[]> => {
  try {
    //  :     
    //   Geohash Firestore GeoPoint   
    const { properties } = await getProperties(1000); //    
    
    return properties.filter(property => {
      const distance = calculateDistance(
        centerLat, centerLng,
        property.location.lat, property.location.lng
      );
      return distance <= radiusKm;
    });
  } catch (error) {
    console.error('   :', error);
    throw new Error('     .');
  }
};

