interface HomePageProps {
  searchTerm?: string;
  addressSearch?: string;
  propertyNumberSearch?: string; //   
  filters?: {
    type: string;
    propertyType: string;
    area: string;
    price: string;
    deposit: string;
  };
  onPropertyAdded?: (properties: Property[]) => void;
  newProperties?: Property[];
  onMapReset?: () => void; //    
}

export interface HomePageRef {
  resetMap: () => void;
}

const HomePage = forwardRef<HomePageRef, HomePageProps>(({ 
  searchTerm = '', 
  addressSearch = '',
  propertyNumberSearch = '',
  filters = { type: '', propertyType: '', area: '', price: '', deposit: '' },
  onPropertyAdded,
  newProperties = [],
  onMapReset
}, ref) => {
  console.log('HomePage  ');
  console.log('filters:', filters);
  
  const [selectedPropertyForDetail, setSelectedPropertyForDetail] = useState<Property | null>(null);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [propertyToDelete, setPropertyToDelete] = useState<Property | null>(null);
  const [selectedClusterProperties, setSelectedClusterProperties] = useState<Property[]>([]);
  const mapRef = useRef<GoogleMapRef>(null);

  // Firebase   
  const { properties: firebaseProperties, loading, error, loadProperties, refreshProperties } = useProperties();

  //      
  useImperativeHandle(ref, () => ({
    resetMap: () => {
      console.log('HomePage - resetMap ');
      console.log('mapRef.current:', mapRef.current);
      if (mapRef.current) {
        //   false   
        
        //   
        setSelectedClusterProperties([]);
        
        //      (    )
        const koreaCenter = { lat: 36.3504, lng: 127.3845 };
        mapRef.current.setCenter(koreaCenter);
        //        
        
        //  
        if (mapRef.current.resetMarkers) {
          mapRef.current.resetMarkers();
        }
        
        console.log('   -   ');
      } else {
        console.log('mapRef.current null');
      }
    }
  }));

  //     - 
  const handleMarkerClick = useCallback((property: Property) => {
    console.log('  :', property);
    //     
    requestAnimationFrame(() => {
      setSelectedClusterProperties(prev => {
        //       
        if (prev.length === 1 && prev[0].id === property.id) {
          return prev;
        }
        return [property];
      });
    });
  }, []);

  //    - 
  const handleClusterClick = useCallback((properties: Property[]) => {
    console.log(' , :', properties);
    //     
    requestAnimationFrame(() => {
      setSelectedClusterProperties(prev => {
        //       
        if (prev.length === properties.length && 
            prev.every((p, i) => p.id === properties[i].id)) {
          return prev;
        }
        return properties;
      });
    });
  }, []);

  //      - 
  const handleFilterChange = useCallback(() => {
    console.log('HomePage useEffect -  :', filters);
    
    //   (   )
    console.log('getFilteredProperties():', getFilteredProperties());
    //      
    setSelectedClusterProperties([]);
    
    //      
    const filteredProperties = getFilteredProperties();
    if (filteredProperties.length > 0 && mapRef.current) {
      //      (  )
      const koreaProperties = filteredProperties.filter(property => {
        const lat = property.location?.lat || 0;
        const lng = property.location?.lng || 0;
        return lat >= 33.5 && lat <= 38.8 && lng >= 124.5 && lng <= 131.9;
      });
      
      if (koreaProperties.length > 0) {
        //      
        const totalLat = koreaProperties.reduce((sum, property) => sum + (property.location?.lat || 0), 0);
        const totalLng = koreaProperties.reduce((sum, property) => sum + (property.location?.lng || 0), 0);
        const avgLat = totalLat / koreaProperties.length;
        const avgLng = totalLng / koreaProperties.length;
        
        console.log('   (  ):', { lat: avgLat, lng: avgLng });
        
        //      
        mapRef.current.setCenter({ lat: avgLat, lng: avgLng });
        
        //       (    )
        if (koreaProperties.length === 1) {
          mapRef.current.setZoom(Math.max(15, mapRef.current.getZoom())); //    
        } else if (koreaProperties.length <= 3) {
          mapRef.current.setZoom(Math.max(13, mapRef.current.getZoom())); //    
        } else {
          mapRef.current.setZoom(Math.max(11, mapRef.current.getZoom())); //    
        }
      } else {
        //         (  )
        const koreaCenter = { lat: 36.3504, lng: 127.3845 };
        mapRef.current.setCenter(koreaCenter);
        //      
        console.log('       .');
      }
      
      //    (  )
      setTimeout(() => {
        if (mapRef.current && mapRef.current.resetMarkers) {
          mapRef.current.resetMarkers();
        }
      }, 500);
    } else if (filteredProperties.length === 0 && mapRef.current) {
      //      (   )
      console.log('   -  ');
      
      //    (resetMarkers  )
      //    
    }
  }, [filters, mapRef]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    handleFilterChange();
  }, [handleFilterChange]);

  //    Firebase  
  useEffect(() => {
    loadProperties();
  }, [loadProperties]);

  //    Firebase 
  useEffect(() => {
    if (newProperties.length > 0) {
      refreshProperties();
    }
  }, [newProperties, refreshProperties]);

  //   (Firebase  +   )
  const allProperties = useMemo(() => [...firebaseProperties, ...newProperties], [firebaseProperties, newProperties]);

  //   
  console.log('firebaseProperties :', firebaseProperties.length);
  console.log('newProperties :', newProperties.length);
  console.log('allProperties :', allProperties.length);
  console.log('allProperties:', allProperties.map(p => ({ 
    id: p.id, 
    title: p.title, 
    type: p.type, 
    price: p.price, 
    deposit: p.deposit 
  })));

  //       
  useEffect(() => {
    if (propertyNumberSearch && propertyNumberSearch.trim()) {
      const foundProperty = allProperties.find(property => 
        property.id.toLowerCase() === propertyNumberSearch.toLowerCase().trim()
      );
      
      if (foundProperty) {
        console.log('  :', foundProperty);
        //      
        // setSelectedPropertyForDetail(foundProperty);
        
        //      (  )
        if (mapRef.current) {
          const propertyLocation = {
            lat: foundProperty.location.lat,
            lng: foundProperty.location.lng
          };
          
          //      (  )
          const isInKorea = propertyLocation.lat >= 33.5 && propertyLocation.lat <= 38.8 &&
                           propertyLocation.lng >= 124.5 && propertyLocation.lng <= 131.9;
          
          if (isInKorea) {
            mapRef.current.setCenter(propertyLocation);
            //          
            mapRef.current.setZoom(Math.max(15, mapRef.current.getZoom()));
          } else {
            //       (  )
            const koreaCenter = { lat: 36.3504, lng: 127.3845 };
            mapRef.current.setCenter(koreaCenter);
            //      
            console.log('       .');
          }
        }
      } else {
        console.log('   :', propertyNumberSearch);
        //      
        setSelectedPropertyForDetail(null);
      }
    }
  }, [propertyNumberSearch, allProperties]);

  //    (  )
  // const preloadImage = (src: string): Promise<void> => {
  //   return new Promise((resolve, reject) => {
  //     const img = new Image();
  //     img.onload = () => resolve();
  //     img.onerror = () => reject(new Error(`Failed to load image: ${src}`));
  //     img.src = src;
  //   });
  // };

  // SimpleMap  
  useEffect(() => {
    console.log('HomePage  ');
    console.log('MapSection ');
    console.log('GoogleMap  ');
  }, []);

  const handlePropertyCardClick = (property: Property) => {
    setSelectedPropertyForDetail(property);
  };

  const handleCloseDetailModal = () => {
    setSelectedPropertyForDetail(null);
  };

  const handlePropertyUpdate = (updatedProperty: Property) => {
    //   
    console.log(' :', updatedProperty);
    setSelectedPropertyForDetail(null);
  };

  const formatPrice = (price: number, isMobile: boolean = false) => {
    console.log('formatPrice :', price, typeof price);
    
    if (!price || price <= 0) {
      console.log(' 0 ');
      return isMobile ? '' : '  ';
    }
    
    if (price >= 100000000) {
      // 1  
      const eok = Math.floor(price / 100000000);
      const man = Math.floor((price % 100000000) / 10000);
      if (man > 0) {
        const result = isMobile ? `${eok}${man}` : `${eok} ${man}`;
        console.log('1  :', result);
        return result;
      }
      const result = isMobile ? `${eok}` : `${eok}`;
      console.log('1 :', result);
      return result;
    } else if (price >= 10000) {
      // 1  1  
      const result = isMobile ? `${Math.floor(price / 10000)}` : `${Math.floor(price / 10000)}`;
      console.log('1  :', result);
      return result;
    } else if (price > 0) {
      // 1  
      const result = isMobile ? `${price}` : `${price.toLocaleString()}`;
      console.log('1  :', result);
      return result;
    }
    
    console.log(' ');
    return isMobile ? '' : '  ';
  };

  const cleanPropertyTitle = (title: string) => {
    return title.replace(/[^\w\s-]/g, '').trim();
  };

  const maskAddress = (address: string) => {
    const parts = address.split(' ');
    if (parts.length >= 3) {
      return `${parts[0]} ${parts[1]} ${parts[2]}`;
    }
    return address;
  };

  const getFilteredProperties = useCallback(() => {
    let filtered = allProperties;

    //  
    if (searchTerm) {
      filtered = filtered.filter(property =>
        property.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        property.address.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    //   
    if (addressSearch) {
      filtered = filtered.filter(property =>
        property.address.toLowerCase().includes(addressSearch.toLowerCase())
      );
    }

    //   
    if (propertyNumberSearch) {
      filtered = filtered.filter(property =>
        property.id.toLowerCase().includes(propertyNumberSearch.toLowerCase())
      );
    }

    //   (/)
    if (filters.type) {
      filtered = filtered.filter(property => property.type === filters.type);
    }

    //   (///)
    if (filters.propertyType) {
      filtered = filtered.filter(property => property.propertyType === filters.propertyType);
    }

    //   (Header  : "10,20"  "10~20")
    if (filters.area) {
      console.log('===     ===');
      console.log('  :', filters.area);
      const areaValues = filters.area.split(',');
      console.log(' :', areaValues);
      if (areaValues.length === 1) {
        //   
        const areaValue = areaValues[0];
        console.log('  :', areaValue);
        if (areaValue.includes('~')) {
          //   (: "10~20")
          const parts = areaValue.split('~');
          console.log('  :', parts);
          if (parts.length === 2) {
            const min = parseFloat(parts[0].replace(/[~]/g, ''));
            const max = parseFloat(parts[1].replace(/[~]/g, ''));
            console.log('   :', min, '~', max);
            console.log('  :', typeof min, typeof max);
            console.log('  :', !isNaN(min), !isNaN(max));
            
            filtered = filtered.filter(property => {
              // property.area   
              const area = Math.round(property.area / 3.3058);
              const isInRange = area >= min && area <= max;
              console.log(` ${property.id} : ${area}(${Math.round(property.area)}m), : ${min}~${max}, : ${isInRange}`);
              return isInRange;
            });
          }
        } else {
          //   (: "10")
          const area = parseFloat(areaValue.replace(/[~]/g, ''));
          console.log('   :', area);
          filtered = filtered.filter(property => {
            // property.area   
            const propertyArea = Math.round(property.area / 3.3058);
            const isMatch = propertyArea === area;
            console.log(` ${property.id} : ${propertyArea}(${Math.round(property.area)}m), : ${area}, : ${isMatch}`);
            return isMatch;
          });
        }
      } else if (areaValues.length === 2) {
        //    ()
        const [min, max] = areaValues.map(a => parseFloat(a.replace(/[~]/g, '')));
        console.log('   :', min, '~', max);
        console.log('   :', typeof min, typeof max);
        console.log('   :', !isNaN(min), !isNaN(max));
        
        filtered = filtered.filter(property => {
          // property.area   
          const area = Math.round(property.area / 3.3058);
          const isInRange = area >= min && area <= max;
          console.log(` ${property.id} : ${area}(${Math.round(property.area)}m), : ${min}~${max}, : ${isInRange}`);
          return isInRange;
        });
      }
      console.log('     :', filtered.length);
      console.log('===     ===');
    }

    //   (Header  : "8,10"  "8~10")
    if (filters.price) {
      console.log('===     ===');
      console.log('  :', filters.price);
      const priceValues = filters.price.split(',');
      console.log(' :', priceValues);
      if (priceValues.length === 1) {
        //   
        const priceValue = priceValues[0];
        console.log('  :', priceValue);
        if (priceValue.includes('~')) {
          //   (: "8~10")
          const parts = priceValue.split('~');
          console.log('  :', parts);
          if (parts.length === 2) {
            const min = parseFloat(parts[0].replace(/[~]/g, '')) * 100000000;
            const max = parseFloat(parts[1].replace(/[~]/g, '')) * 100000000;
            console.log('   :', min, '~', max);
            console.log('  :', typeof min, typeof max);
            console.log('  :', !isNaN(min), !isNaN(max));
            
            filtered = filtered.filter(property => {
              const price = property.price * 10000; //     
              const isInRange = price >= min && price <= max;
              console.log(` ${property.id} : ${property.price} (${price}), : ${min}~${max}, : ${isInRange}`);
              return isInRange;
            });
          }
        } else {
          //   (: "8")
          const price = parseFloat(priceValue.replace(/[~]/g, '')) * 100000000;
          console.log('   :', price);
          filtered = filtered.filter(property => property.price * 10000 === price);
        }
      } else if (priceValues.length === 2) {
        //    ()
        const [min, max] = priceValues.map(p => parseFloat(p.replace(/[~]/g, '')) * 100000000);
        console.log('   :', min, '~', max);
        console.log('   :', typeof min, typeof max);
        console.log('   :', !isNaN(min), !isNaN(max));
        
        filtered = filtered.filter(property => {
          const price = property.price * 10000; //     
          const isInRange = price >= min && price <= max;
          console.log(` ${property.id} : ${property.price} (${price}), : ${min}~${max}, : ${isInRange}`);
          return isInRange;
        });
      }
      console.log('     :', filtered.length);
      console.log('===     ===');
    }

    //   (Header  : "1000,2000"  "1000~2000")
    if (filters.deposit) {
      console.log('===     ===');
      console.log('  :', filters.deposit);
      const depositValues = filters.deposit.split(',');
      console.log(' :', depositValues);
      
      if (depositValues.length === 1) {
        //   
        const depositValue = depositValues[0];
        console.log('  :', depositValue);
        
        if (depositValue.includes('~')) {
          //   (: "1000~2000")
          const parts = depositValue.split('~');
          console.log('  :', parts);
          if (parts.length === 2) {
            //    (     )
            let min = 0;
            let max = 0;
            
            //  
            if (parts[0] === '') {
              min = 0; //   0
            } else if (parts[0].includes('')) {
              min = parseFloat(parts[0].replace(/[~]/g, '')) * 1000;
            } else {
              min = parseFloat(parts[0].replace(/[~]/g, ''));
            }
            
            //  
            if (parts[1] === '') {
              max = Number.MAX_SAFE_INTEGER; //   
            } else if (parts[1].includes('')) {
              max = parseFloat(parts[1].replace(/[~]/g, '')) * 1000;
            } else {
              max = parseFloat(parts[1].replace(/[~]/g, ''));
            }
            
            console.log('    ():', min, '~', max);
            console.log('  :', typeof min, typeof max);
            console.log('  :', !isNaN(min), !isNaN(max));
            
            filtered = filtered.filter(property => {
              const deposit = property.deposit || 0; //    
              const isInRange = deposit >= min && deposit <= max;
              console.log(` ${property.id} : ${deposit}, : ${min}~${max}, : ${isInRange}`);
              return isInRange;
            });
          }
        } else {
          //   (: "1000")
          let deposit = 0;
          if (depositValue.includes('')) {
            deposit = parseFloat(depositValue.replace(/[~]/g, '')) * 1000; //    
          } else {
            deposit = parseFloat(depositValue.replace(/[~]/g, '')); //   
          }
          console.log('    ():', deposit);
          filtered = filtered.filter(property => {
            const propertyDeposit = property.deposit || 0; //    
            const isMatch = propertyDeposit === deposit;
            console.log(` ${property.id} : ${propertyDeposit}, : ${deposit}, : ${isMatch}`);
            return isMatch;
          });
        }
      } else if (depositValues.length === 2) {
        //    ()
        const [min, max] = depositValues.map(d => {
          if (d.includes('')) {
            return parseFloat(d.replace(/[~]/g, '')) * 1000; //    
          } else {
            return parseFloat(d.replace(/[~]/g, '')); //   
          }
        });
        console.log('    ():', min, '~', max);
        console.log('   :', typeof min, typeof max);
        console.log('   :', !isNaN(min), !isNaN(max));
        
        filtered = filtered.filter(property => {
          const deposit = property.deposit || 0; //    
          const isInRange = deposit >= min && deposit <= max;
          console.log(` ${property.id} : ${deposit}, : ${min}~${max}, : ${isInRange}`);
          return isInRange;
        });
      }
      console.log('     :', filtered.length);
      console.log('===     ===');
    }

    //       ,     
    //     
    return filtered;
  }, [allProperties, searchTerm, addressSearch, propertyNumberSearch, filters]);

  const handleConfirmDelete = () => {
    if (propertyToDelete) {
      console.log(' :', propertyToDelete.id);
      setShowDeleteConfirm(false);
      setPropertyToDelete(null);
    }
  };

  const handleCancelDelete = () => {
    setShowDeleteConfirm(false);
    setPropertyToDelete(null);
  };

  const displayProperties = useMemo(() => getFilteredProperties(), [
    getFilteredProperties
  ]);
  
  const listProperties = useMemo(() => 
    selectedClusterProperties.length > 0 ? selectedClusterProperties : displayProperties,
    [selectedClusterProperties, displayProperties]
  );

  console.log('displayProperties:', displayProperties.length, ' ');
  console.log('listProperties:', listProperties.length, '  ( )');

  // GoogleMap  props  -    
  const mapProps = useMemo(() => ({
    properties: displayProperties,
    onMarkerClick: handleMarkerClick,
    onClusterClick: handleClusterClick
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }), [displayProperties]); // handleMarkerClick, handleClusterClick  

  return (
    <>
      {loading && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0, 0, 0, 0.5)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 1000
        }}>
          <div style={{
            background: 'white',
            padding: '2rem',
            borderRadius: '8px',
            textAlign: 'center'
          }}>
            <div style={{ marginBottom: '1rem' }}>  ...</div>
            <div style={{ fontSize: '0.9rem', color: '#6b7280' }}>
              Firebase    .
            </div>
          </div>
        </div>
      )}
      
      {error && (
        <div style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          background: '#fee2e2',
          color: '#dc2626',
          padding: '1rem',
          borderRadius: '8px',
          border: '1px solid #fecaca',
          boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
          zIndex: 1000,
          maxWidth: '400px'
        }}>
          <div style={{ fontWeight: 'bold', marginBottom: '0.5rem' }}>  </div>
          <div style={{ fontSize: '0.9rem', marginBottom: '0.5rem' }}>{error}</div>
          <button
            onClick={() => window.location.reload()}
            style={{
              background: '#dc2626',
              color: 'white',
              border: 'none',
              padding: '0.5rem 1rem',
              borderRadius: '4px',
              cursor: 'pointer',
              fontSize: '0.8rem'
            }}
          >
            
          </button>
        </div>
      )}

      <HomeContainer>
        <MapSection>
          <GoogleMap
            {...mapProps}
            ref={mapRef}
          />
        </MapSection>
        
        <PropertyListSection>
          <PropertyListContainer>
            {listProperties.length === 0 ? (
              <EmptyState>
                <EmptyIcon></EmptyIcon>
                <p>  .</p>
                {/* isAdmin && (
                  <AdminButton onClick={() => setSelectedPropertyForDetail({} as Property)}>
                      
                  </AdminButton>
                ) */}
              </EmptyState>
            ) : (
              listProperties.map(property => {
                // localStorage   
                const savedMainImages = localStorage.getItem(`mainImages_${property.id}`);
                let displayImages = property.images || [];
                
                console.log(` ${property.id}   (PC):`, {
                  propertyImages: property.images,
                  savedMainImages: savedMainImages,
                  displayImages: displayImages,
                  hasImages: displayImages && displayImages.length > 0,
                  firstImageUrl: displayImages && displayImages.length > 0 ? displayImages[0] : ''
                });
                
                if (savedMainImages && savedMainImages !== 'null' && savedMainImages !== '[]') {
                  try {
                    const parsedImages = JSON.parse(savedMainImages);
                    if (Array.isArray(parsedImages) && parsedImages.length > 0) {
                      displayImages = parsedImages;
                      console.log(` ${property.id} localStorage  :`, displayImages);
                    }
                  } catch (error) {
                    console.error('  :', error);
                  }
                }
                
                console.log(` ${property.id}  displayImages:`, displayImages);
                
                return (
                  <PropertyCard 
                    key={property.id}
                    onClick={() => handlePropertyCardClick(property)}
                  >
                    <PropertyImage>
                      {displayImages && displayImages.length > 0 ? (
                        <>
                          <img 
                            src={displayImages[0]} 
                            alt={property.title}
                            onError={(e) => {
                              console.log('PC   :', displayImages[0]);
                              const target = e.target as HTMLImageElement;
                              target.style.display = 'none';
                              const parent = target.parentElement;
                              if (parent && !parent.querySelector('.fallback-icon')) {
                                const fallback = document.createElement('div');
                                fallback.className = 'fallback-icon';
                                fallback.textContent = '';
                                fallback.style.cssText = `
                                  position: absolute;
                                  top: 50%;
                                  left: 50%;
                                  transform: translate(-50%, -50%);
                                  font-size: 2rem;
                                  color: white;
                                  z-index: 1;
                                `;
                                parent.appendChild(fallback);
                              }
                            }}
                            onLoad={(e) => {
                              console.log('PC   :', displayImages[0]);
                              const target = e.target as HTMLImageElement;
                              const parent = target.parentElement;
                              const fallback = parent?.querySelector('.fallback-icon');
                              if (fallback) {
                                fallback.remove();
                              }
                            }}
                            loading="eager"
                            style={{
                              width: '100%',
                              height: '100%',
                              objectFit: 'cover',
                              borderRadius: '8px',
                              transition: 'opacity 0.3s ease'
                            }}
                          />
                          {/*    */}
                          <div className="image-loading" style={{
                            position: 'absolute',
                            top: '50%',
                            left: '50%',
                            transform: 'translate(-50%, -50%)',
                            fontSize: '1rem',
                            color: 'white',
                            background: 'rgba(0,0,0,0.5)',
                            padding: '0.25rem 0.5rem',
                            borderRadius: '4px',
                            zIndex: 2,
                            display: 'none'
                          }}>
                            ...
                          </div>
                        </>
                      ) : (
                        <div className="fallback-icon" style={{
                          position: 'absolute',
                          top: '50%',
                          left: '50%',
                          transform: 'translate(-50%, -50%)',
                          fontSize: '2rem',
                          color: 'white',
                          zIndex: 1
                        }}></div>
                      )}
                    </PropertyImage>
                    <PropertyInfo>
                      <PropertyNumber>
                        <PropertyNumberLabel></PropertyNumberLabel>
                        <PropertyNumberValue>{property.id}</PropertyNumberValue>
                      </PropertyNumber>
                      <PropertyAddress>
                        {maskAddress(property.address)}
                      </PropertyAddress>
                      <PropertyTitle>
                        {cleanPropertyTitle(property.title)}
                      </PropertyTitle>
                      <PropertyDetails>
                        <DetailTag>
                          {property.type === 'sale' ? '' : ''}
                        </DetailTag>
                        <DetailTag> {Math.round(property.area / 3.3058)}</DetailTag>
                        {property.floor && <DetailTag>{property.floor}</DetailTag>}
                        {property.parking !== undefined && (
                          <DetailTag> {property.parking ? '' : ''}</DetailTag>
                        )}
                        {property.elevator !== undefined && (
                          <DetailTag> {property.elevator ? '' : ''}</DetailTag>
                        )}
                      </PropertyDetails>
                      <PropertyPrice>
                        {property.type === 'rent' ? (
                          <>
                            {(() => {
                              console.log('===     ===');
                              console.log(' ID:', property.id);
                              console.log(' :', property.type);
                              console.log(':', property.deposit);
                              console.log(':', property.price);
                              console.log(' :', property.deposit && property.deposit > 0);
                              console.log(' :', property.price && property.price > 0);
                              return null;
                            })()}
                            {(property.deposit && property.deposit > 0) && (
                              <PriceButton>
                                 {formatPrice(property.deposit, window.innerWidth <= 768)}
                              </PriceButton>
                            )}
                            {(property.price && property.price > 0) && (
                              <PriceButton>
                                 {formatPrice(property.price, window.innerWidth <= 768)}
                              </PriceButton>
                            )}
                            {(!property.deposit || property.deposit <= 0) && (!property.price || property.price <= 0) && (
                              <PriceButton>
                                {window.innerWidth <= 768 ? '' : '  '}
                              </PriceButton>
                            )}
                          </>
                        ) : (
                          <PriceButton>
                             {formatPrice(property.price, window.innerWidth <= 768)}
                          </PriceButton>
                        )}
                      </PropertyPrice>
                    </PropertyInfo>
                    {/* isAdmin && (
                      <AdminButtons>
                        <AdminButton 
                          onClick={(e) => {
                            e.stopPropagation();
                            handleEditProperty(property);
                          }}
                        >
                          
                        </AdminButton>
                        <AdminButton 
                          variant="delete"
                          onClick={(e) => {
                            e.stopPropagation();
                            handleDeleteProperty(property);
                          }}
                        >
                          
                        </AdminButton>
                      </AdminButtons>
                    ) */}
                  </PropertyCard>
                );
              })
            )}
          </PropertyListContainer>
        </PropertyListSection>
      </HomeContainer>

      {/*      */}
      <PropertyListContainer>
        {listProperties.length === 0 ? (
          <EmptyState>
            <EmptyIcon></EmptyIcon>
            <p>  .</p>
            {/* isAdmin && (
              <AdminButton onClick={() => setSelectedPropertyForDetail({} as Property)}>
                  
              </AdminButton>
            ) */}
          </EmptyState>
        ) : (
          listProperties.map(property => {
            // localStorage   
            const savedMainImages = localStorage.getItem(`mainImages_${property.id}`);
            let displayImages = property.images || [];
            
            console.log(` ${property.id}   ():`, {
              propertyImages: property.images,
              savedMainImages: savedMainImages,
              displayImages: displayImages,
              hasImages: displayImages && displayImages.length > 0,
              firstImageUrl: displayImages && displayImages.length > 0 ? displayImages[0] : '',
              isMobile: window.innerWidth <= 768,
              userAgent: navigator.userAgent
            });
            
            if (savedMainImages && savedMainImages !== 'null' && savedMainImages !== '[]') {
              try {
                const parsedImages = JSON.parse(savedMainImages);
                if (Array.isArray(parsedImages) && parsedImages.length > 0) {
                  displayImages = parsedImages;
                  console.log(` ${property.id} localStorage   ():`, displayImages);
                }
              } catch (error) {
                console.error('  :', error);
              }
            }
            
            //  2, 3    (   )
            if ((!displayImages || displayImages.length === 0) && 
                (property.id.includes('P002') || property.id.includes('P003') || 
                 listProperties.indexOf(property) === 1 || listProperties.indexOf(property) === 2)) {
              const testImages = [
                'https://images.unsplash.com/photo-1560518883-ce09059eeffa?w=400&h=300&fit=crop',
                'https://images.unsplash.com/photo-1564013799919-ab600027ffc6?w=400&h=300&fit=crop'
              ];
              displayImages = testImages;
              console.log(` ${property.id}   :`, displayImages);
              
              // localStorage 
              const storageKey = `mainImages_${property.id}`;
              localStorage.setItem(storageKey, JSON.stringify(testImages));
            }
            
            console.log(` ${property.id}  displayImages ():`, displayImages);
            
            return (
              <PropertyCard 
                key={property.id}
                onClick={() => handlePropertyCardClick(property)}
              >
                <PropertyImage>
                  {displayImages && displayImages.length > 0 ? (
                    <>
                      {/*    */}
                      <div className="image-loading" style={{
                        position: 'absolute',
                        top: '50%',
                        left: '50%',
                        transform: 'translate(-50%, -50%)',
                        fontSize: '1rem',
                        color: 'white',
                        background: 'rgba(0,0,0,0.7)',
                        padding: '0.5rem 1rem',
                        borderRadius: '8px',
                        zIndex: 2,
                        display: 'block'
                      }}>
                         ...
                      </div>
                      <img 
                        src={displayImages[0]} 
                        alt={property.title}
                        onError={(e) => {
                          console.log('   :', displayImages[0]);
                          console.log(' URL :', displayImages[0]?.startsWith('http'));
                          const target = e.target as HTMLImageElement;
                          target.style.display = 'none';
                          const parent = target.parentElement;
                          const loading = parent?.querySelector('.image-loading') as HTMLElement;
                          if (loading) {
                            loading.style.display = 'none';
                          }
                          if (parent && !parent.querySelector('.fallback-icon')) {
                            const fallback = document.createElement('div');
                            fallback.className = 'fallback-icon';
                            fallback.textContent = '';
                            fallback.style.cssText = `
                              position: absolute;
                              top: 50%;
                              left: 50%;
                              transform: translate(-50%, -50%);
                              font-size: 2rem;
                              color: white;
                              z-index: 1;
                            `;
                            parent.appendChild(fallback);
                          }
                        }}
                        onLoad={(e) => {
                          console.log('   :', displayImages[0]);
                          const target = e.target as HTMLImageElement;
                          const parent = target.parentElement;
                          const loading = parent?.querySelector('.image-loading') as HTMLElement;
                          const fallback = parent?.querySelector('.fallback-icon');
                          if (loading) {
                            loading.style.display = 'none';
                          }
                          if (fallback) {
                            fallback.remove();
                          }
                          target.style.opacity = '1';
                        }}
                        loading="eager"
                        style={{
                          width: '100%',
                          height: '100%',
                          objectFit: 'cover',
                          borderRadius: '8px',
                          transition: 'opacity 0.5s ease',
                          opacity: '0'
                        }}
                      />
                    </>
                  ) : (
                    <div className="fallback-icon" style={{
                      position: 'absolute',
                      top: '50%',
                      left: '50%',
                      transform: 'translate(-50%, -50%)',
                      fontSize: '2rem',
                      color: 'white',
                      zIndex: 1
                    }}></div>
                  )}
                </PropertyImage>
                <PropertyInfo>
                  <PropertyNumber>
                    <PropertyNumberLabel></PropertyNumberLabel>
                    <PropertyNumberValue>{property.id}</PropertyNumberValue>
                  </PropertyNumber>
                  <PropertyAddress>
                    {maskAddress(property.address)}
                  </PropertyAddress>
                  <PropertyTitle>
                    {cleanPropertyTitle(property.title)}
                  </PropertyTitle>
                  <PropertyDetails>
                    <DetailTag>
                      {property.type === 'sale' ? '' : ''}
                    </DetailTag>
                    <DetailTag> {Math.round(property.area / 3.3058)}</DetailTag>
                    {property.floor && <DetailTag>{property.floor}</DetailTag>}
                    {property.parking !== undefined && (
                      <DetailTag> {property.parking ? '' : ''}</DetailTag>
                    )}
                    {property.elevator !== undefined && (
                      <DetailTag> {property.elevator ? '' : ''}</DetailTag>
                    )}
                  </PropertyDetails>
                  <PropertyPrice>
                    {property.type === 'rent' ? (
                      <>
                        {(() => {
                          console.log('===      ===');
                          console.log(' ID:', property.id);
                          console.log(' :', property.type);
                          console.log(':', property.deposit);
                          console.log(':', property.price);
                          console.log(' :', property.deposit && property.deposit > 0);
                          console.log(' :', property.price && property.price > 0);
                          return null;
                        })()}
                        {(property.deposit && property.deposit > 0) && (
                          <PriceButton>
                             {formatPrice(property.deposit, true)}
                          </PriceButton>
                        )}
                        {(property.price && property.price > 0) && (
                          <PriceButton>
                             {formatPrice(property.price, true)}
                          </PriceButton>
                        )}
                        {(!property.deposit || property.deposit <= 0) && (!property.price || property.price <= 0) && (
                          <PriceButton>
                            
                          </PriceButton>
                        )}
                      </>
                    ) : (
                      <PriceButton>
                         {formatPrice(property.price, true)}
                      </PriceButton>
                    )}
                  </PropertyPrice>
                </PropertyInfo>
                {/* isAdmin && (
                  <AdminButtons>
                    <AdminButton 
                      onClick={(e) => {
                        e.stopPropagation();
                        handleEditProperty(property);
                      }}
                    >
                      
                    </AdminButton>
                    <AdminButton 
                      variant="delete"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleDeleteProperty(property);
                      }}
                    >
                      
                    </AdminButton>
                  </AdminButtons>
                ) */}
              </PropertyCard>
            );
          })
        )}
      </PropertyListContainer>
      
      {selectedPropertyForDetail && (
        <PropertyDetailModal
          property={selectedPropertyForDetail}
          onClose={handleCloseDetailModal}
          onPropertyUpdate={handlePropertyUpdate}
          isAdmin={false}
        />
      )}

      {/*    */}
      {showDeleteConfirm && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: 'rgba(0, 0, 0, 0.5)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 1000
        }}>
          <div style={{
            background: 'white',
            padding: '2rem',
            borderRadius: '8px',
            maxWidth: '400px',
            width: '90%'
          }}>
            <h3 style={{ marginBottom: '1rem', color: '#dc2626' }}> </h3>
            <p style={{ marginBottom: '1.5rem' }}>
                 ?
            </p>
            <div style={{ display: 'flex', gap: '1rem', justifyContent: 'flex-end' }}>
              <button
                onClick={handleCancelDelete}
                style={{
                  padding: '0.5rem 1rem',
                  background: '#6b7280',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}
              >
                
              </button>
              <button
                onClick={handleConfirmDelete}
                style={{
                  padding: '0.5rem 1rem',
                  background: '#dc2626',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}
              >
                
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
});

export default HomePage; 