import React, { useState } from 'react';
import styled from 'styled-components';
import { addTestProperty, addTestRentProperty, getProperties, addImagesToExistingProperties, forceRefreshFirebaseData, addImagesToSpecificProperties } from '../firebase/propertyService';
import { useFirebase } from '../contexts/FirebaseContext';

const DebugContainer = styled.div`
  position: fixed;
  top: 10px;
  right: 10px;
  background: #1f2937;
  color: white;
  padding: 1rem;
  border-radius: 8px;
  font-family: monospace;
  font-size: 0.875rem;
  max-width: 400px;
  z-index: 9999;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  
  @media (max-width: 768px) {
    top: 5px;
    right: 5px;
    left: 5px;
    max-width: none;
    font-size: 0.75rem;
    padding: 0.75rem;
  }
`;

const DebugTitle = styled.h3`
  margin: 0 0 1rem 0;
  color: #10b981;
  font-size: 1rem;
`;

const DebugButton = styled.button`
  background: #10b981;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
  font-size: 0.75rem;

  &:hover {
    background: #059669;
  }

  &:disabled {
    background: #6b7280;
    cursor: not-allowed;
  }
  
  @media (max-width: 768px) {
    padding: 0.75rem 0.5rem;
    font-size: 0.7rem;
    margin: 0.2rem;
    min-height: 44px; /*     */
  }
`;

const DebugLog = styled.div`
  background: #111827;
  padding: 0.5rem;
  border-radius: 4px;
  margin-top: 0.5rem;
  max-height: 200px;
  overflow-y: auto;
  font-size: 0.75rem;
  white-space: pre-wrap;
`;

const CloseButton = styled.button`
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  background: none;
  border: none;
  color: #9ca3af;
  cursor: pointer;
  font-size: 1rem;

  &:hover {
    color: white;
  }
`;

interface FirebaseDebuggerProps {
  onClose: () => void;
}

const FirebaseDebugger: React.FC<FirebaseDebuggerProps> = ({ onClose }) => {
  const [isLoading, setIsLoading] = useState(false);
  const [logs, setLogs] = useState<string[]>([]);
  const { user } = useFirebase();

  const addLog = (message: string) => {
    const timestamp = new Date().toLocaleTimeString();
    setLogs(prev => [...prev, `[${timestamp}] ${message}`]);
  };

  const testAddProperty = async () => {
    if (!user) {
      addLog('   .');
      return;
    }

    setIsLoading(true);
    addLog('     ...');
    
    try {
      const propertyId = await addTestProperty();
      addLog(`     ! ID: ${propertyId}`);
    } catch (error: any) {
      addLog(`     : ${error.message}`);
      addLog(`    : ${error.code || 'N/A'}`);
    } finally {
      setIsLoading(false);
    }
  };

  const testAddRentProperty = async () => {
    if (!user) {
      addLog('   .');
      return;
    }

    setIsLoading(true);
    addLog('     ...');
    
    try {
      const propertyId = await addTestRentProperty();
      addLog(`     ! ID: ${propertyId}`);
      addLog('    .');
    } catch (error: any) {
      addLog(`     : ${error.message}`);
      addLog(`    : ${error.code || 'N/A'}`);
    } finally {
      setIsLoading(false);
    }
  };

  const refreshPage = () => {
    addLog('   ...');
    window.location.reload();
  };

  const testGetProperties = async () => {
    setIsLoading(true);
    addLog('    ...');
    
    try {
      const { properties } = await getProperties();
      addLog(`    !  ${properties.length}`);
      
      if (properties.length > 0) {
        properties.forEach((property, index) => {
          addLog(`    ${index + 1}: ${property.title}`);
          addLog(`     - : ${property.type}`);
          addLog(`     - : ${property.price}`);
          addLog(`     - : ${property.deposit || ''}`);
          addLog(`     - : ${property.images?.length || 0}`);
          if (property.type === 'rent') {
            addLog(`     -  : ${formatPrice(property.deposit || 0)}`);
            addLog(`     -  : ${formatPrice(property.price)}`);
          }
        });
      } else {
        addLog('     .');
      }
    } catch (error: any) {
      addLog(`    : ${error.message}`);
      addLog(`    : ${error.code || 'N/A'}`);
    } finally {
      setIsLoading(false);
    }
  };

  const addImagesToProperties = async () => {
    if (!user) {
      addLog('   .');
      return;
    }

    setIsLoading(true);
    addLog('     ...');
    
    try {
      await addImagesToExistingProperties();
      addLog('     !');
      addLog('    .');
    } catch (error: any) {
      addLog(`   : ${error.message}`);
      addLog(`    : ${error.code || 'N/A'}`);
    } finally {
      setIsLoading(false);
    }
  };

  const forceRefreshData = async () => {
    setIsLoading(true);
    addLog(' Firebase    ...');
    
    try {
      const { properties } = await forceRefreshFirebaseData();
      addLog(`   !  ${properties.length} `);
      
      if (properties.length > 0) {
        properties.forEach((property, index) => {
          addLog(`    ${index + 1}: ${property.title}`);
          addLog(`     - : ${property.type}`);
          addLog(`     - : ${property.images?.length || 0}`);
          if (property.images && property.images.length > 0) {
            addLog(`     -  URL: ${property.images[0]}`);
          }
        });
      } else {
        addLog('     .');
      }
      
      addLog('    .');
    } catch (error: any) {
      addLog(`   : ${error.message}`);
      addLog(`    : ${error.code || 'N/A'}`);
    } finally {
      setIsLoading(false);
    }
  };

  const addImagesToSpecific = async () => {
    if (!user) {
      addLog('   .');
      return;
    }

    setIsLoading(true);
    addLog(' 2,3    ...');
    addLog(`  : ${window.innerWidth <= 768 ? 'YES' : 'NO'}`);
    
    try {
      await addImagesToSpecificProperties();
      addLog(' 2,3    !');
      
      //  localStorage  
      if (window.innerWidth <= 768) {
        addLog('  localStorage ...');
        localStorage.removeItem('properties_cache');
        localStorage.removeItem('last_sync_time');
        addLog('    ');
      }
      
      addLog('    .');
    } catch (error: any) {
      addLog(`   : ${error.message}`);
      addLog(`    : ${error.code || 'N/A'}`);
    } finally {
      setIsLoading(false);
    }
  };

  const formatPrice = (price: number) => {
    if (price >= 100000000) {
      const eok = Math.floor(price / 100000000);
      const man = Math.floor((price % 100000000) / 10000);
      if (man > 0) {
        return `${eok} ${man}`;
      }
      return `${eok}`;
    } else if (price >= 10000) {
      return `${Math.floor(price / 10000)}`;
    } else if (price > 0) {
      return `${price.toLocaleString()}`;
    }
    return '  ';
  };

  const clearLogs = () => {
    setLogs([]);
  };

  return (
    <DebugContainer>
      <CloseButton onClick={onClose}></CloseButton>
      <DebugTitle> Firebase </DebugTitle>
      
      <div>
        <div style={{ marginBottom: '0.5rem', fontSize: '0.75rem' }}>
          : {user ? ` ${user.email}` : '  '}
        </div>
        
        <DebugButton 
          onClick={testAddProperty} 
          disabled={isLoading || !user}
        >
             
        </DebugButton>
        
        <DebugButton 
          onClick={testAddRentProperty} 
          disabled={isLoading || !user}
        >
             
        </DebugButton>
        
        <DebugButton 
          onClick={testGetProperties} 
          disabled={isLoading}
        >
             
        </DebugButton>
        
        <DebugButton 
          onClick={clearLogs} 
          disabled={isLoading}
        >
            
        </DebugButton>
        
        <DebugButton 
          onClick={addImagesToProperties} 
          disabled={isLoading || !user}
        >
              
        </DebugButton>
        
        <DebugButton 
          onClick={addImagesToSpecific} 
          disabled={isLoading || !user}
        >
           2,3   
        </DebugButton>
        
        <DebugButton 
          onClick={forceRefreshData} 
          disabled={isLoading}
        >
           Firebase   
        </DebugButton>
        
        <DebugButton 
          onClick={refreshPage} 
          disabled={isLoading}
        >
            
        </DebugButton>
      </div>

      {logs.length > 0 && (
        <DebugLog>
          {logs.join('\n')}
        </DebugLog>
      )}
    </DebugContainer>
  );
};

export default FirebaseDebugger;
